import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { FormCreateWizardComponent } from './views/form-create-wizard/form-create-wizard.component';
import { FormCreateWizardPackageView } from './views/form-create-wizard-package-view';


export class FormCreateWizardPackage extends Package {
    constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
        super(gsp);
    }

    activate(state) {
        this.gsp.eventBus.on(null, null, 'Command:openMetadataGuide:WebDevMetadata', this, (data) => this.showModal(state, data));
    }

    getComponent(): Type<any> {
        return FormCreateWizardComponent;
    }

    showModal(state, data) {
        const packageView = new FormCreateWizardPackageView(state, this.cfr, this.injector);
        const comp = packageView.getElement().instance as FormCreateWizardComponent;
        comp.metadataDto = data;
        const modalPanel = this.gsp.workspace.addModalPanel({
            item: {
                element: packageView.getElement(),
                modalOptions: {
                    title: '',
                    width: 1000,
                    height: 520,
                    showButtons: false
                }
            }
        });

        comp.modalClosed.subscribe(() => modalPanel.destroy());

        // -
        comp.createFormEvent.subscribe((formData) => {
            this.gsp.workspace.open(formData);
        });
    }

}





