import { Component, OnInit, Input } from '@angular/core';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { filter, findIndex } from 'lodash';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';
import { cloneDeep } from 'lodash';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-select-be-actions',
  templateUrl: './select-be-actions.component.html',
  styleUrls: ['./select-be-actions.component.css'],
  providers: [FormCreateWizardService]
})
export class SelectBeActionsComponent implements OnInit {
  @Input() data;
  actionList = [];
  selectedIdList = [];
  // 
  // private deleteMgrActionId = '9sEsnADPFkCRK6ZBC0OwiA';
  // private modifyMgrActionId = '8Wna6KaBQEOHpRJfmGMBYQ';
  // private queryMgrActionId = 'm_6xIwJwoUy7qzsuZ6_S5A';
  // private retrieveDefaultMgrActionId = 'Tji_dbrLtEKQlOLRpOJj9Q';
  // private retrieveMgrActionId = 'fQbpcO7bIEGoRdTZZIyrag';

  presetActionList = ['9sEsnADPFkCRK6ZBC0OwiA', '8Wna6KaBQEOHpRJfmGMBYQ', 'm_6xIwJwoUy7qzsuZ6_S5A',
    'Tji_dbrLtEKQlOLRpOJj9Q', 'fQbpcO7bIEGoRdTZZIyrag'];
  public selectableSettings: SelectableSettings = {
    checkboxOnly: true,
    mode: 'multiple'
  };
  constructor(private formCreateService: FormCreateWizardService) { }

  ngOnInit() {
    if (this.data && this.data.beMetadata) {
      const beMetadata = this.data.beMetadata;
      const content = JSON.parse(beMetadata.metadata.content);
      this.actionList = content.BizMgrActions;
      if (this.data.selectedBeActions.length === 0) {
        this.selectedIdList = cloneDeep(this.presetActionList);
      } else {
        this.data.selectedBeActions.forEach(action => {
          this.selectedIdList.push(action.ID);
        });
      }
    }
  }

  clickNextStep = () => {
    const subject = new Subject<any>();
    // this.data.selectedBeActions = filter(self.actionList, function (o) {
    //   return findIndex(self.selectedIdList, function (id) { return id === o.ID; }) >= 0;
    // });

    // BE VM
    this.data.selectedBeMetadata = cloneDeep(this.data.beMetadata);
    this.formCreateService.parseBeFields2VM(this.data.selectedBeMetadata, this.data.selectedBeFields,
      this.data.selectedBeActions).subscribe(result => {
        if (result) {
          this.data.dgViewModel = this.formCreateService.parseViewModel(result);
          console.log('dgVM', this.data.dgViewModel);
          subject.next(true);
        }
      });
    return subject;

  }
  changeCheckbox(ID) {
    const index = this.selectedIdList.indexOf(ID);
    if (index >= 0) {
      this.selectedIdList.splice(index, 1);
    } else {
      this.selectedIdList.push(ID);
    }
    const self = this;
    this.data.selectedBeActions = filter(self.actionList, function (o) {
      return findIndex(self.selectedIdList, function (id) { return id === o.ID; }) >= 0;
    });
  }

}



