import { Component, Output, EventEmitter, OnInit } from '@angular/core';
import { DataModelSelectComponent } from '../data-model-select';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';
import { FormBasicInfoComponent } from '../form-basic-info/form-basic-info.component';
import { FormTemplateSelectComponent } from '../form-template-select/form-template-select.component';
import { SelectBeFieldsComponent } from '../select-be-fields/select-be-fields.component';
import { FormConfirmComponent } from '../form-confirm/form-confirm.component';
import { SelectBeActionsComponent } from '../select-be-actions/select-be-actions.component';
import { SelectShowFieldsComponent } from '../select-show-fields/select-show-fields.component';
import { MetadataDto } from '@gsp-lcm/metadata-selector';

@Component({
  selector: 'webide-form-create-wizard',
  templateUrl: './form-create-wizard.component.html',
  styleUrls: ['./form-create-wizard.component.css'],
  providers: [FormCreateWizardService]
})
export class FormCreateWizardComponent implements OnInit {
  modalClosed = new EventEmitter();
  private _metadata: MetadataDto;
  @Output() createFormEvent = new EventEmitter<any>();
  set metadataDto(metadataDto: MetadataDto) {
    this._metadata = metadataDto;
    this.formInfo.basicInfo = metadataDto;
  }

  formInfo = { // 
    basicInfo: {},
    templateInfo: {
      selectedTplCategory: '',
      selectedTpl: null
    },
    dataModel: {
      selectedModelSource: '',
      selectedViewModel: null,
      modelUpload: {
        fileName: '',
        fileContent: {}
      },
      beMetadata: null,
      selectedBeFields: {},
      selectedBeActions: [],
      selectedBeMetadata: null,
      dgViewModel: null
    },
    showFields: {
      card_selected: {}, //  
      list_selected: [], //  
      listcard_selected: { //  
        showFieldsInGrid: {},
        showFieldsInCard: {}
      }
    }

  };
  steps = [
    {
      stepId: 'selectTemplate',
      title: '',
      component: FormTemplateSelectComponent,
      data: this.formInfo.templateInfo,
      show: true,
      nextStep: 'selectDataModel',
      showFinishBtn: false,
      showCancelBtn: true,
      showLastStepBtn: false,
      showNextStepBtn: true
    },
    {
      stepId: 'selectDataModel',
      title: '',
      component: DataModelSelectComponent,
      data: this.formInfo,
      nextStep: 'selectBeFields',
      show: true,
      showFinishBtn: false,
      showCancelBtn: true,
      showLastStepBtn: true,
      showNextStepBtn: true
    },
    {
      stepId: 'selectBeFields',
      title: 'BE',
      component: SelectBeFieldsComponent,
      data: this.formInfo.dataModel,
      show: true,
      nextStep: 'selectBeActions',
      showFinishBtn: false,
      showCancelBtn: true,
      showLastStepBtn: true,
      showNextStepBtn: true
    }, {
      stepId: 'selectBeActions',
      title: 'BE',
      component: SelectBeActionsComponent,
      data: this.formInfo.dataModel,
      show: true,
      nextStep: 'selectShowFields',
      showFinishBtn: false,
      showCancelBtn: true,
      showLastStepBtn: true,
      showNextStepBtn: true
    },
    {
      stepId: 'selectShowFields',
      title: '',
      component: SelectShowFieldsComponent,
      data: this.formInfo,
      nextStep: 'formConfirm',
      show: true,
      showFinishBtn: false,
      showCancelBtn: true,
      showLastStepBtn: true,
      showNextStepBtn: true
    },
    {
      stepId: 'formConfirm',
      title: '',
      component: FormConfirmComponent,
      data: {},
      show: true,
      nextStep: '',
      showFinishBtn: true,
      showCancelBtn: true,
      showLastStepBtn: true,
      showNextStepBtn: false

    }];

  constructor(private formCreateService: FormCreateWizardService) { }

  ngOnInit() {
  }
  /**
   * 
   */
  clickCloseModal() {
    this.modalClosed.emit();
  }
  /**
   * 
   */
  clickFinishBtn() {
    console.log(this.formInfo);
    this.modalClosed.emit();

    const domJson = this.formInfo.templateInfo.selectedTpl.domJson;

    // code/name
    domJson.module.id = domJson.module.code = this._metadata.code;
    domJson.module.name = domJson.module.caption = this._metadata.name;
    domJson.module.creationDate = '2018-08-30 10:56:00';

    // VMDOM
    const vm = this.formInfo.dataModel.dgViewModel;
    domJson.module.schemas = [vm];

    // 
    this.formCreateService.saveForm(domJson, this._metadata).subscribe(result => {
      if (result.code === 'success') {
        this.createFormEvent.emit('/' + this._metadata.relativePath + '/' + this._metadata.fileName); // ,
        this.clickCloseModal();
      } else {
        alert(result.message);
      }

    });
  }
}



