import { Component, OnInit, Input } from '@angular/core';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';

@Component({
  selector: 'app-form-template-select',
  templateUrl: './form-template-select.component.html',
  styleUrls: ['./form-template-select.component.css']
})
export class FormTemplateSelectComponent implements OnInit {
  @Input() data;
  tplCategories = []; // 
  tplList = []; // 
  searchName = '';
  constructor(private formCreateService: FormCreateWizardService) { }

  ngOnInit() {
    this.tplCategories = this.formCreateService.queryTemplateCatetory();
    this.data.selectedTplCategory = this.tplCategories[0].key;
    this.searchTemplate();
  }

  changeSelectTpl(template) {
    this.data.selectedTpl = template;
  }
  searchTemplate() {
    this.formCreateService.getTemplateList(this.data.selectedTplCategory, this.searchName).subscribe((data) => {
      this.tplList = data || [];
      if (!this.data.selectedTpl) {
        this.data.selectedTpl = this.tplList.length > 0 ? this.tplList[0] : '';
      }

    });
  }
  changeTplCategory() {
    this.searchTemplate();
  }

}



