import { Component, OnInit, Input } from '@angular/core';
import { of } from 'rxjs/observable/of';
import { find, filter, findIndex } from 'lodash';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';

@Component({
  selector: 'app-select-show-fields',
  templateUrl: './select-show-fields.component.html',
  styleUrls: ['./select-show-fields.component.css'],
  providers: [FormCreateWizardService]
})
export class SelectShowFieldsComponent implements OnInit {

  @Input() data;
  treeData = []; // 
  expandedKeys: any[] = ['0'];
  selectedTreeKeys = [];
  selectedTreeKey = '';
  fieldsMap = {};
  cardGridData = {};
  gridData = [];
  selectedTpl;
  card_selected = {}; //  
  list_selected = []; //  
  listcard_selected = { //  
    showFieldsInGrid: {},
    showFieldsInCard: {}
  };
  mainTable = ''; // code
  public selectableSettings: SelectableSettings = {
    checkboxOnly: true,
    mode: 'multiple'
  };
  public children = (dataItem: any) => of(dataItem.Children);
  public hasChildren = (dataItem: any): boolean => !!dataItem.Children;

  constructor(private formCreateService: FormCreateWizardService) { }

  ngOnInit() {
    this.selectedTpl = this.data.templateInfo.selectedTpl;
    const dgViewModel = this.data.dataModel.dgViewModel;

    this.card_selected = this.data.showFields.card_selected;
    this.list_selected = this.data.showFields.list_selected;
    this.listcard_selected = this.data.showFields.listcard_selected;

    if (this.selectedTpl.category === 'card') {
      //   
      if (!dgViewModel) { return; }
      this.treeData = this.getTablesTree(dgViewModel);
      const mainTableCode = this.treeData[0].code;
      this.mainTable = this.selectedTreeKey = mainTableCode;
      this.selectedTreeKeys.push(mainTableCode);

      this.checkCardFieldsInGrid();
    } else if (this.selectedTpl.category === 'list') {
      //  
      const mainFieldsObject = dgViewModel.fields;
      this.mainTable = dgViewModel.code;
      this.gridData = this.getGridFields(mainFieldsObject);

      this.list_selected = this.checkFieldsInGridData(this.list_selected, this.gridData);
    } else if (this.selectedTpl.category === 'listcard') {
      //  
      const mainFieldsObject = dgViewModel.fields;
      this.mainTable = dgViewModel.code;
      this.gridData = this.getGridFields(mainFieldsObject);

      this.listcard_selected.showFieldsInGrid = this.checkObjectFieldsInGrid(this.listcard_selected.showFieldsInGrid, this.gridData);
      this.listcard_selected.showFieldsInCard = this.checkObjectFieldsInGrid(this.listcard_selected.showFieldsInCard, this.gridData);
    }
  }
  getTablesTree(viewModel) {
    const { children, ...parentTable } = viewModel;
    this.fieldsMap[parentTable.code] = parentTable;

    this.cardGridData[parentTable.code] = this.getGridFields(parentTable.fields);
    if (!this.card_selected[parentTable.code]) {
      this.card_selected[parentTable.code] = [];
      // (ID)
      this.cardGridData[parentTable.code].forEach(field => {
        if (!field.code.includes('ID')) {
          this.card_selected[parentTable.code].push(field.labelID);
        }
      });
    }
    if (children && Object.keys(children).length > 0) {
      parentTable['Children'] = [];
      Object.keys(children).forEach(child => {
        const childTree = this.getTablesTree(children[child]);
        parentTable['Children'] = parentTable['Children'].concat(childTree);
      });
    }
    return [parentTable];
  }
  getGridFields(fieldsObject) {
    const gridData = [];
    if (fieldsObject && Object.keys(fieldsObject).length > 0) {
      Object.keys(fieldsObject).forEach(fieldCode => {
        const { refObject, ...field } = fieldsObject[fieldCode];
        gridData.push(field);

        if (refObject && refObject.fields && Object.keys(refObject.fields).length > 0) {
          Object.keys(refObject.fields).forEach(refFieldCode => {
            gridData.push(refObject.fields[refFieldCode]);
          });
        }
      });
    }
    return gridData;
  }

  handleTreeSelection(event): void {
    const curCode = event.dataItem.code;
    this.selectedTreeKeys = [curCode];
    this.selectedTreeKey = curCode;
  }

  clickNextStep = () => {
    if (this.selectedTpl.category === 'card') {
      return this.addFields2CardTpl();
    } else if (this.selectedTpl.category === 'list') {
      if (this.list_selected.length === 0) {
        alert('');
        return false;
      }
      return this.addFields2ListTpl();
    } else if (this.selectedTpl.category === 'listcard') {
      return this.addFields2ListCardTpl();
    }
  }

  /**
   * 
   */
  addFields2ListTpl(): boolean {
    const domJson = this.selectedTpl.domJson;
    if (!domJson) {
      return false;
    }
    // dom
    const contentContainer = domJson.module.components.length > 0 && domJson.module.components[0];
    const gridContainer = find(contentContainer.contents, function (o) { return o.id === 'gridContainer'; });
    const dataGrid = gridContainer && gridContainer.contents.length > 0 && gridContainer.contents[0];

    // 
    dataGrid.fields = [];
    this.list_selected.forEach(labelID => {
      const selectedField = find(this.gridData, function (o) { return o.labelID === labelID; });
      if (!selectedField) {
        return;
      }
      const gridFieldMetadata = this.formCreateService.getMetadataByType('GridField');
      gridFieldMetadata['id'] = 'grid_' + selectedField.labelID;
      gridFieldMetadata['dataField'] = selectedField.labelID;
      gridFieldMetadata['caption'] = selectedField.name;

      dataGrid.dataSource = this.mainTable;
      dataGrid.id = 'dataGrid_' + this.mainTable;
      dataGrid.fields.push(gridFieldMetadata);
    });
    return true;
  }

  /**
   * 
   */
  addFields2CardTpl(): boolean {
    const domJson = this.selectedTpl.domJson;
    if (!domJson) {
      return false;
    }

    // domtab
    const pageContainer = domJson.module.components.length > 0 && domJson.module.components[0];
    const mainContainer = find(pageContainer.contents, function (o) { return o.id === 'mainContainer'; });
    const mainContents = mainContainer && mainContainer.contents;
    const tabContainer = find(pageContainer.contents, function (o) { return o.id === 'childTab'; });
    mainContents.length = 0;
    tabContainer.contents = [];

    const headerContainer = find(pageContainer.contents, function (o) { return o.id === 'pageHeader'; });
    if (headerContainer) {
      headerContainer.contents[0].title = this.data.basicInfo.name;
    }

    const tableCodes = Object.keys(this.card_selected);
    if (!tableCodes || tableCodes.length === 0) {
      return true;
    }

    tableCodes.forEach(tableCode => {
      const selectedFieldLables = this.card_selected[tableCode];
      if (!selectedFieldLables || selectedFieldLables.length === 0) {
        return;
      }
      if (tableCode === this.mainTable && selectedFieldLables && selectedFieldLables.length > 0) {
        // 
        selectedFieldLables.forEach(labelID => {
          const selectedField = find(this.cardGridData[tableCode], function (o) { return o.labelID === labelID; });
          if (!selectedField) {
            return;
          }
          const fieldMetadata = this.formCreateService.getMetadataByType(selectedField.dataType);
          if (fieldMetadata) {
            fieldMetadata['id'] = 'card_' + selectedField.labelID;
            fieldMetadata['title'] = selectedField.name;
            fieldMetadata['appearance'] = { class: 'col-md-4' };

            mainContents.push(fieldMetadata);
          }
        });
      } else {
        // tabtab
        const tabPage = this.formCreateService.getMetadataByType('tabPage');
        if (tabPage) {
          tabPage['id'] = 'tabPage_' + tableCode;
          tabPage['title'] = this.fieldsMap[tableCode].name;

          const dataGrid = this.formCreateService.getMetadataByType('DataGrid');
          selectedFieldLables.forEach(labelID => {
            const selectedField = find(this.cardGridData[tableCode], function (o) { return o.labelID === labelID; });
            if (!selectedField) {
              return;
            }
            const gridFieldMetadata = this.formCreateService.getMetadataByType('GridField');
            if (gridFieldMetadata) {
              gridFieldMetadata['id'] = 'gridField_' + selectedField.labelID;
              gridFieldMetadata['dataField'] = selectedField.labelID;
              gridFieldMetadata['caption'] = selectedField.name;

              dataGrid['fields'].push(gridFieldMetadata);
            }
          });
          dataGrid['id'] = 'dataGrid_' + tableCode;
          dataGrid['appearance'] = { class: 'my-2' };
          dataGrid['dataSource'] = this.mainTable + '.' + tableCode;

          tabPage['contents'].push(dataGrid);
          if (tabPage['contents'].length > 0) {
            tabContainer.contents.push(tabPage);
          }
        }
      }

    });
    return true;
  }
  /**
   * -
   */
  addFields2ListCardTpl() {
    const domJson = this.selectedTpl.domJson;
    if (!domJson) {
      return false;
    }

    // dom
    const pageContainer = domJson.module.components.length > 0 && domJson.module.components[0];
    const gridCardContainer = find(pageContainer.contents, function (o) { return o.id === 'gridCardContainer'; });
    const dataGrid = gridCardContainer && gridCardContainer.contents.length > 0 && gridCardContainer.contents[0];

    // 
    dataGrid.fields = [];
    const showFieldsInGrid = this.listcard_selected.showFieldsInGrid;
    Object.keys(showFieldsInGrid).forEach(labelID => {
      if (!showFieldsInGrid[labelID]) {
        return;
      }
      const selectedField = find(this.gridData, function (o) { return o.labelID === labelID; });
      if (!selectedField) {
        return;
      }
      const gridFieldMetadata = this.formCreateService.getMetadataByType('GridField');
      if (gridFieldMetadata) {
        gridFieldMetadata['id'] = 'gridField_' + selectedField.labelID;
        gridFieldMetadata['dataField'] = selectedField.labelID;
        gridFieldMetadata['caption'] = selectedField.name;

        dataGrid.id = 'dataGrid_' + this.mainTable;
        dataGrid.fields.push(gridFieldMetadata);
        dataGrid.dataSource = this.mainTable;
      }
    });

    // 
    const showFieldsInCard = this.listcard_selected.showFieldsInCard;
    const cardPanel = gridCardContainer && gridCardContainer.contents.length > 0 && gridCardContainer.contents[1];
    cardPanel.contents = [];
    Object.keys(showFieldsInCard).forEach(labelID => {
      if (!showFieldsInCard[labelID]) {
        return;
      }
      const selectedField = find(this.gridData, function (o) { return o.labelID === labelID; });
      if (!selectedField) {
        return;
      }
      const fieldMetadata = this.formCreateService.getMetadataByType(selectedField.dataType);
      if (fieldMetadata) {
        fieldMetadata['id'] = 'card_' + selectedField.labelID;
        fieldMetadata['title'] = selectedField.name;

        cardPanel.contents.push(fieldMetadata);
      }
    });
    return true;
  }

  /**
   * ()
   */
  private checkCardFieldsInGrid() {
    if (!this.card_selected || Object.keys(this.card_selected).length === 0) {
      return {};
    }
    Object.keys(this.card_selected).forEach(tableCode => {
      const curFieldList = this.cardGridData[tableCode];
      if (!curFieldList) {
        delete this.card_selected[tableCode];
      } else {
        const curFieldCodeList = [];
        curFieldList.forEach(field => {
          curFieldCodeList.push(field.labelID);
        });
        this.card_selected[tableCode] = filter(this.card_selected[tableCode], function (o) { return curFieldCodeList.indexOf(o) >= 0; });
      }

    });
  }
  /**
   * ()
   */
  private checkFieldsInGridData(selected, gridData) {
    if (!selected) {
      return [];
    }
    for (let i = 0; i < selected.length; i++) {
      const labelID = selected[i];
      const index = findIndex(gridData, function (o) { return o.labelID === labelID; });
      if (index < 0) {
        selected.splice(i, 1);
        i--;
      }
    }
    return selected;
  }

  /**
   * ()
   */
  private checkObjectFieldsInGrid(selected, gridData) {
    if (!selected) {
      return {};
    }
    const labelList = Object.keys(selected);
    for (let i = 0; i < labelList.length; i++) {
      const labelID = labelList[i];
      const labelIndex = findIndex(gridData, function (o) { return o.labelID === labelID; });
      if (labelIndex < 0) {
        selected[labelID] = false;
      }
    }
    return selected;
  }

}



