import { Component, OnInit, Input, OnChanges, ComponentFactoryResolver, Injector, Output, EventEmitter } from '@angular/core';
import { DataModelService } from './services/data-model-service';
import { SelectMetadataComponent } from '@gsp-lcm/metadata-selector';
import { BsModalService } from '@farris/ui';
import { cloneDeep } from 'lodash';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';

@Component({
  selector: 'webide-data-model-select',
  templateUrl: './data-model-select.component.html',
  styleUrls: ['./data-model-select.component.css'],
  providers: [DataModelService, FormCreateWizardService]
})
export class DataModelSelectComponent implements OnInit {
  @Input() data;
  viewModelList = []; // 
  @Output() changeStep = new EventEmitter<any>();
  constructor(private dataModelService: DataModelService, private formCreateService: FormCreateWizardService,
    private resolver: ComponentFactoryResolver, private modalService: BsModalService, private injector: Injector) { }

  ngOnInit() {
    if (!this.data.dataModel.selectedModelSource) {
      this.data.dataModel.selectedModelSource = 'beEntity';
    }
    if (this.data.dataModel.selectedModelSource === 'viewModel') {
      this.queryViewModelList();
    }
  }
  /**
     * 
     */
  private queryViewModelList() {
    this.dataModelService.queryViewModelList().subscribe((data) => {
      this.viewModelList = data || [];
      //  ---> 
      if ((!this.data.dataModel.selectedViewModel || Object.keys(this.data.dataModel.selectedViewModel).length === 0) &&
        this.viewModelList.length > 0) {
        this.data.dataModel.selectedViewModel = this.viewModelList[0];
      }

    });
  }
  /**
  * 
  */
  changeModelSource(source) {
    if (this.data.dataModel.selectedModelSource === 'beEntity' && source !== 'beEntity') {
      this.changeStep.emit([{
        stepId: 'selectDataModel',
        nextStep: 'selectShowFields'
      }, {
        stepId: 'selectBeFields',
        show: false
      },
      {
        stepId: 'selectBeActions',
        show: false
      }]);
    }

    this.data.dataModel.selectedModelSource = source;
    if (source === 'viewModel') {
      // 
      this.queryViewModelList();

    } else if (source === 'beEntity') {
      this.changeStep.emit([{
        stepId: 'selectDataModel',
        nextStep: 'selectBeFields'
      }, {
        stepId: 'selectBeFields',
        show: true
      }, {
        stepId: 'selectBeActions',
        show: true,
        nextStep: 'selectShowFields'
      }]);
    }
  }

  /**
  * 
  * @param viewModel
  */
  changeViewModel(viewModel) {
    this.data.dataModel.selectedViewModel = viewModel;
  }


  /**
   * BE
   */
  openMetadataSelect() {
    const compFactory = this.resolver.resolveComponentFactory(SelectMetadataComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.metadataPath = this.data.basicInfo.relativePath;
    compRef.instance.typeName = 'GSPBusinessEntity';
    compRef.instance.selectedMetadata.subscribe($event => {
      if (!$event || !$event.metadata) {
        return;
      }
      this.data.dataModel.beMetadata = $event;
      this.data.dataModel.selectedBeMetadata = cloneDeep($event);
      console.log('beMetadata:', $event);
    });
    const modalConfig = {
      title: '',
      width: 870,
      height: 565,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModalEmitter.subscribe((data) => {
      dialog.close();
    });
  }

  validation = () => {
    if (this.data.dataModel.selectedModelSource === 'viewModel' && !this.data.dataModel.selectedViewModel) {
      return false;
    } else if (this.data.dataModel.selectedModelSource === 'manual') {
      return this.checkFormRequired('validation-manual');
    } else if (this.data.dataModel.selectedModelSource === 'beEntity') {
      return this.checkFormRequired('validation-beEntity');
    }
    return true;
  }

  private checkFormRequired(className) {
    const self = this;
    let forms;
    forms = document.getElementsByClassName(className);
    if (forms && forms.length > 0) {
      const form = forms[0];
      let flag = false;
      if (form.checkValidity()) {
        flag = true;
      }
      form.classList.add('was-validated');
      return flag;
    } else {
      return true;
    }
  }

  /**
   * 
   * @param event
   */
  getUpload(event) {
    if (event.target.files[0]) {
      const file = event.target.files[0];
      if (file.type !== 'application/json') {
        return;
      }
      this.data.dataModel.selectedViewModel.fileName = file.name;
      const self = this;
      const reader = new FileReader();
      reader.readAsText(file);
      reader.onload = function () {
        const resObject = JSON.parse(<string>(reader.result));
        // console.log(':', resObject);
        self.data.dataModel.selectedViewModel.fileContent = resObject;
      };
    }
  }

  clickNextStep = () => {
    if (this.data.dataModel.selectedModelSource === 'viewModel') {
      this.data.dataModel.dgViewModel = this.formCreateService.parseViewModel(this.data.dataModel.selectedViewModel);
    }
    // const subject = new Subject<any>();
    // subject.n;
    return true;
  }
}



