import { Component, OnInit, Input } from '@angular/core';
import { of } from 'rxjs/observable/of';
@Component({
  selector: 'app-form-basic-info',
  templateUrl: './form-basic-info.component.html',
  styleUrls: ['./form-basic-info.component.css']
})
export class FormBasicInfoComponent implements OnInit {
  @Input() data;
  isFormCodeValid = true;

  constructor() { }

  ngOnInit() { }
  checkFormCode() {
    const reg2 = new RegExp('^[a-zA-Z_\x7f-\xff][a-zA-Z0-9_\x7f-\xff]*$');
    if (!reg2.test(this.data.formCode)) {
      this.isFormCodeValid = false;

    } else {
      this.isFormCodeValid = true;
    }
  }

  validation = () => {
    const self = this;
    let forms;
    forms = document.getElementsByClassName('validation-basic');
    if (forms && forms.length > 0) {
      const form = forms[0];
      let flag = false;
      if (form.checkValidity() && this.isFormCodeValid) {
        flag = true;
      }
      form.classList.add('was-validated');
      return flag;
    } else {
      return true;
    }
  }
}



