import {PackageView} from '@farris/ide-devkit';
import {ComponentFactoryResolver, Injector} from '@angular/core';
import {FormCreateWizardComponent} from './form-create-wizard/form-create-wizard.component';

export class FormCreateWizardPackageView extends PackageView {
  constructor(serializedState: any, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(serializedState);
    this.element = cfr.resolveComponentFactory(FormCreateWizardComponent).create(injector);
  }
}



