import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { TemplateEntity } from '../entity/template';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { DataItemEntity, DataModelEntity } from '../entity/DataModel';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { filter } from 'lodash';

@Injectable()
export class FormCreateWizardService {
    constructor(private http: HttpClient, private metadataService: GSPMetadataService) {
    }


    /**
     * mock 
     */
    getTemplateList(categoryKey, templateName): Observable<TemplateEntity[]> {
        const subject = new Subject<TemplateEntity[]>();
        this.http.get<TemplateEntity[]>('assets/mock-data/templateListData.json').subscribe(data => {
            if (categoryKey) {
                data = filter(data, function (o) { return o.category === categoryKey; });
            }
            if (templateName) {
                data = filter(data, function (o) { return o.templateName.includes(templateName); });
            }
            subject.next(data);
        });
        return subject;
    }
    /**
     * mock 
    */
    queryViewModelList(): Observable<any[]> {
        return this.http.get<any[]>('assets/mock-data/viewModelListData.json');
    }

    /**
     * 
     */
    queryTemplateCatetory() {
        return [{
            key: '', value: ''
        }, {
            key: 'list', value: ''
        }, {
            key: 'card', value: ''
        }, {
            key: 'listcard', value: ''
        }];
    }

    /**
     * 
     */
    saveForm(domJson, basicInfo): Observable<any> {
        const result = new Subject<any>();
        this.metadataService.InitializeMetadataEntity(basicInfo).subscribe(initResult => {
            const initContent = JSON.parse(initResult.content);
            initContent.Contents = JSON.stringify(domJson);
            initResult.content = JSON.stringify(initContent);
            this.metadataService.CreateMetadata(initResult).subscribe(() => {
                result.next({
                    code: 'success',
                    message: ''
                });
            }, error => {
                result.next({
                    code: 'error',
                    message: '' + (error.message || '')
                });
            });

        }, error => {
            result.next({
                code: 'error',
                message: '' + (error.message || '')
            });
        });
        return result;
    }


    /**
   * BE()
   */
    getBeMainObject(beMetadata) {
        if (!beMetadata || !beMetadata.metadata) {
            return;
        }
        const metadataContent = JSON.parse(beMetadata.metadata.content);
        return metadataContent.MainObject;
    }
    /**
     * BEbeActionsbeMetadataAPIVM
     */
    parseBeFields2VM(beMetadata, selectedBeFields, selectedBeActions): Observable<any> {
        if (!beMetadata || !beMetadata.metadata) {
            return;
        }
        const metadataContent = JSON.parse(beMetadata.metadata.content);
        // 
        metadataContent.BizMgrActions = selectedBeActions;

        // 
        metadataContent.MainObject = this.getBeMainObjectByIds(metadataContent.MainObject, selectedBeFields);

        console.log('metadata', beMetadata);

        beMetadata.metadata.content = JSON.stringify(metadataContent);

        // API VM
        const url = environment.SERVER_IP + '/api/dev/main/v1.0/viewmodel/convertBizEntity';
        const convertBeInfo = {
            bizEntityJson: beMetadata.metadata.content,
            bePkgName: beMetadata.packageHeader.name,
            beId: beMetadata.metadata.id
        };
        return this.http.put(url, convertBeInfo);

    }

    /**
    * DgViewModel
    */
    public parseViewModel(viewModel) {
        if (!viewModel) {
            return;
        }
        // 
        const dataModel = this.parseElements(viewModel['MainObject']);
        return dataModel;
    }

    /**
     * 
     * @param mainObject
     */
    private parseElements(mainObject) {
        const dataModel: DataModelEntity = {
            id: mainObject.ID,
            code: mainObject.Code,
            name: mainObject.Name,
            fields: {},
            children: {}
        };
        const fieldList = {};
        const elements = mainObject.ContainElements;
        elements.forEach(element => {
            const itemCode = element.Code;
            let itemObject: DataItemEntity;
            itemObject = {
                id: element.ID,
                dataType: this.adpterDataType(element.MDataType),
                defaultValue: element.DefaultValue,
                name: element.Name,
                primary: false,
                code: element.Code,
                labelID: element.LabelID
            };

            if (element.ID === mainObject.ColumnGenerateID.ElementID) {  // 
                itemObject.primary = true;
            }
            fieldList[itemCode] = itemObject;

            // const idElement = mainObject.IDElement;
            // if (idElement && fieldList[idElement.Code]) {
            //     fieldList[idElement.Code].primary = true;
            // }

            // 
            if (element.ContainEnumValues && element.ContainEnumValues.length > 0) {
                itemObject.enumValues = element.ContainEnumValues;
            }

            // 
            if (element.ChildAssociations.length > 0 && element.ChildAssociations[0].RefElementCollection) {
                itemObject.refObject = {
                    id: element.ChildAssociations[0].RefModelID,
                    code: element.ChildAssociations[0].RefModelCode,
                    name: element.ChildAssociations[0].RefModelName,
                    fields: {}
                };
                element.ChildAssociations[0].RefElementCollection.forEach(association => {
                    const assName = association.Code;
                    let assObject: DataItemEntity;
                    assObject = {
                        id: association.ID,
                        dataType: this.adpterDataType(association.MDataType),
                        defaultValue: association.DefaultValue,
                        name: association.Name,
                        primary: false,
                        code: association.Code,
                        labelID: association.LabelID
                    };
                    itemObject.refObject.fields[assName] = assObject;
                });
            }
        });

        // 
        const ContainChildObjects = mainObject.ContainChildObjects;
        if (ContainChildObjects && ContainChildObjects.length > 0) {
            ContainChildObjects.forEach(element => {
                const childElement = this.parseElements(element);
                dataModel.children[element.Code] = childElement;
            });
        }
        dataModel.fields = fieldList;
        return dataModel;
    }




    /**
    * DgViewModelkendo-treeview
    */
    private mappingDgVM2Tree(dataModel: object) {
        const rootObject = {
            ID: dataModel['id'],
            Name: dataModel['name'],
            Code: dataModel['code'],
            labelID: dataModel['code'],
            Children: []
        };
        const fields = dataModel['fields'];
        Object.keys(fields).forEach(key => {
            let isLeaf = true;
            // 
            const refObject = fields[key].refObject;
            let nodeRefObject;
            if (refObject) {
                nodeRefObject = this.mappingDgVM2Tree(refObject);
                isLeaf = false;
            }
            const nodeObject = {
                ID: fields[key].id,
                Name: fields[key].name || '',
                Code: key,
                labelID: fields[key].labelID
            };
            if (!isLeaf) {
                nodeObject['Children'] = [nodeRefObject];
            }

            rootObject.Children.push(nodeObject);
        });

        // 
        const childFields = dataModel['children'];
        if (childFields && Object.keys(childFields).length > 0) {
            const childRoot = {
                ID: '',
                Name: '',
                Code: '',
                labelID: ''
            };
            if (Object.keys(childFields).length > 0) {
                childRoot['Children'] = [];
                Object.keys(childFields).forEach(childField => {
                    const childObject = this.mappingDgVM2Tree(childFields[childField]);
                    childRoot['Children'].push(childObject);
                });
            }


            rootObject.Children.push(childRoot);
        }

        return rootObject;
    }


    /**
     * ID
     */
    getBeMainObjectByIds(mainObject, selectedBeFields) {
        let beFields = [];
        Object.keys(selectedBeFields).forEach(tableCode => {
            if (selectedBeFields[tableCode].length > 0) {
                beFields = beFields.concat(selectedBeFields[tableCode]);
            }
        });
        const containElements = mainObject.ContainElements;
        // 
        for (let i = 0; i < containElements.length; i++) {
            const element = containElements[i];
            if (element.ChildAssociations.length === 0) { // 
                if (beFields.indexOf(element.ID) < 0) {
                    containElements.splice(i, 1);
                    i--;
                    continue;
                }
            } else { // 
                element.ChildAssociations.forEach(child => {
                    const refElementCollection = child.RefElementCollection;
                    let isSelected = false;
                    for (let refIndex = 0; refIndex < refElementCollection.length; refIndex++) {
                        const refElement = refElementCollection[refIndex];
                        if (beFields.indexOf(refElement.ID) < 0) {
                            refElementCollection.splice(refIndex, 1);
                            refIndex--;
                        } else {
                            isSelected = true; // 
                        }
                    }
                    if (!isSelected) {
                        containElements.splice(i, 1);
                        i--;
                    }
                });

            }
        }

        // 
        const containChildObjects = mainObject.ContainChildObjects;
        for (let i = 0; i < containChildObjects.length; i++) {
            let childObject = containChildObjects[i];
            childObject = this.getBeMainObjectByIds(childObject, beFields);
            if (childObject.ContainElements.length === 0 && childObject.ContainChildObjects.length === 0) {
                // 
                containChildObjects.splice(i, 1);
                i--;
            }
        }
        return mainObject;
    }


    /**
     * VM-JIT
     */
    adpterDataType(dataType) {
        switch (dataType) {
            case 'String': case 'Text':
                return 'string';
            case 'Bool': case 'bool':
                return 'boolean';
            case 'Decimal':
                return 'number';
        }
        return dataType;

    }
    /**
     * 
     */
    getMetadataByType(metadataType) {
        switch (metadataType) {
            case 'DataGrid': {
                return {
                    'id': 'DataGrid1',
                    'type': 'DataGrid',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'dataSource': null,
                    'fields': [],
                    'focusedItem': null,
                    'focusedIndex': null,
                    'pagination': null,
                    'identifyField': null,
                    'multiSelect': null,
                    'selectable': null,
                    'itemTemplate': null,
                    'toolBar': null,
                    'sortable': false,
                    'sort': null,
                    'summary': null,
                    'groupable': null,
                    'group': null
                };
            }

            case 'GridField': {
                return {
                    'id': 'col1',
                    'type': 'GridField',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'dataField': '',
                    'displayTemplate': null,
                    'caption': '',
                    'captionTemplate': null,
                    'editor': null,
                    'readonly': false,
                    'require': false,
                    'draggable': false,
                    'frozen': false,
                    'sortable': false,
                    'sortOrder': null,
                    'resizeable': true
                };
            }

            case 'string': {
                return {
                    'id': 'textBox1',
                    'type': 'TextBox',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'title': '',
                    'readonly': false,
                    'require': false,
                    'disable': false,
                    'placeHolder': '',
                    'format': null,
                    'validation': null,
                    'defaultValue': '',
                    'value': null,
                    'maxLength': null
                };
            }
            case 'number': {
                return {
                    'id': 'numericBox1',
                    'type': 'NumericBox',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'title': '',
                    'readonly': false,
                    'require': false,
                    'disable': false,
                    'placeHolder': '',
                    'format': null,
                    'validation': null,
                    'defaultValue': null,
                    'value': null,
                    'maxValue': null,
                    'minValue': 0,
                    'spin': null,
                    'step': 1
                };
            }
            case 'Date': {
                return {
                    'id': 'date1',
                    'type': 'DateBox',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'binding': null,
                    'title': '',
                    'readonly': false,
                    'require': false,
                    'disable': false,
                    'placeHolder': '',
                    'format': null,
                    'validation': null,
                    'defaultValue': null,
                    'value': null,
                    'maxValue': null,
                    'minValue': 0,
                    'spin': null,
                    'step': 1
                };
            }
            case 'tabPage': {
                return {
                    'id': 'tabpage1',
                    'type': 'TabPage',
                    'appearance': null,
                    'border': null,
                    'font': null,
                    'margin': null,
                    'padding': null,
                    'size': null,
                    'contentTemplate': null,
                    'headerTemplate': null,
                    'title': '',
                    'contents': []
                };
            }


        }



    }
}



