import { Component, OnInit, Input } from '@angular/core';
import { FormCreateWizardService } from '../../services/form-create-wizard.service';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { SelectableSettings } from '@progress/kendo-angular-grid';
import { of } from 'rxjs/observable/of';
import { BehaviorSubject, Subject } from 'rxjs';

@Component({
  selector: 'app-select-be-fields',
  templateUrl: './select-be-fields.component.html',
  styleUrls: ['./select-be-fields.component.css']
})
export class SelectBeFieldsComponent implements OnInit {
  public checkedKeys: any[] = [];
  @Input() data;
  treeData: any[] = [];
  gridData = [];
  selectedTreeKeys = [];
  selectedTreeKey = '';
  grid_selected = {};
  treeTables = [];
  readonlyFields = []; // 
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'multiple'
    };
  }
  expandedKeys: any[] = ['0'];
  public selectableSettings: SelectableSettings = {
    checkboxOnly: true,
    mode: 'multiple'
  };
  public children = (dataItem: any) => of(dataItem.Children);
  public hasChildren = (dataItem: any): boolean => !!dataItem.Children;
  constructor(private formCreateService: FormCreateWizardService) { }

  ngOnInit() {
    const beMetadata = this.data.beMetadata;
    if (!beMetadata) {
      return;
    }
    this.grid_selected = this.data.selectedBeFields;
    const mainObject = this.formCreateService.getBeMainObject(beMetadata);
    this.treeData = this.getTablesTree(mainObject);
    this.selectedTreeKey = this.treeData[0].Code;
    this.selectedTreeKeys = [this.selectedTreeKey];

    // mainObjectgrid_selected
    Object.keys(this.grid_selected).forEach(tableCode => {
      if (this.treeTables.indexOf(tableCode) < 0) {
        delete this.grid_selected[tableCode];
      }
    });

  }

  /**
   * 
   */
  clickNextStep = () => {
    let isSelected = false;
    Object.keys(this.grid_selected).forEach(tableCode => {
      if (this.grid_selected[tableCode].length > 0) {
        isSelected = true;
        return;
      }
    });
    if (!isSelected) {
      alert('');
      return false;
    }
    this.data.selectedBeFields = this.grid_selected;
    return true;
  }


  /**
   * 
   */
  getTablesTree(mainObject) {
    const { ContainChildObjects, ColumnGenerateID, Keys, ...parentTable } = mainObject;
    this.treeTables.push(parentTable.Code);
    this.gridData[parentTable.Code] = this.getGridFields(parentTable.ContainElements);

    if (!this.grid_selected[parentTable.Code]) {
      this.grid_selected[parentTable.Code] = [];
      // 
      this.gridData[parentTable.Code].forEach(element => {
        this.grid_selected[parentTable.Code].push(element.ID);
      });
    }

    if (ContainChildObjects && ContainChildObjects.length > 0) {
      parentTable['Children'] = [];
      ContainChildObjects.forEach(child => {
        const childTree = this.getTablesTree(child);
        parentTable['Children'] = parentTable['Children'].concat(childTree);
      });
    }
    // 
    if (ColumnGenerateID) {
      this.readonlyFields.push(ColumnGenerateID.ElementID);
    }
    // 
    if (Keys && Keys.length > 0) {
      Keys.forEach(key => {
        this.readonlyFields.push(key.SourceElement);
      });

    }
    return [parentTable];
  }
  /**
   * 
   * @param fields
   */
  getGridFields(fields) {
    let gridData = [];
    if (fields && fields.length > 0) {
      fields.forEach(field => {
        const { ChildAssociations, ...fieldObject } = field;
        gridData.push(fieldObject);

        if (ChildAssociations && ChildAssociations.length > 0) {
          ChildAssociations.forEach(child => {
            gridData = gridData.concat(child.RefElementCollection);
          });
        }
      });
    }
    return gridData;
  }

  handleTreeSelection(event): void {
    const curCode = event.dataItem.Code;
    this.selectedTreeKeys = [curCode];
    this.selectedTreeKey = curCode;
  }

  changeCheckbox(ID) {
    const index = this.grid_selected[this.selectedTreeKey].indexOf(ID);
    if (index >= 0) {
      this.grid_selected[this.selectedTreeKey].splice(index, 1);
    } else {
      this.grid_selected[this.selectedTreeKey].push(ID);
    }
  }
}



